<script>
import {
  MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
} from "mdb-vue-ui-kit";

export default {
  name: "MyCard3",
  data() {
    return {
      date: null,
    };
  },
  components: {
    MDBCard,
    MDBCardBody,
    MDBCardTitle,
    MDBCardText,
  },
  props: {
    metaData: undefined
  },
  mounted() {
    const fileupload = this.$refs.fileupload;
    fileupload.onclick = function () {
      this.value = null;
    };
    const router = this.$router
    var vm = this
    fileupload.addEventListener("change", function () {
      const reader = new FileReader();
      reader.addEventListener("load", function () {
        const uploaded_image = reader.result;
        router.push({
          name: "main",
          params: { uploaded_image: uploaded_image, metaData: JSON.stringify(vm.metaData) }
        });
      });
      reader.readAsDataURL(this.files[0]);
    });
  },
  methods: {
    openCam() {
      this.$router.push({ name: 'main', params: { metaData: JSON.stringify(this.metaData) } })
    },
    tryTestImage() {

      function toDataURL(src, callback, outputFormat) {
        var img = new Image();
        img.crossOrigin = 'Anonymous';
        img.onload = function () {
          var canvas = document.createElement('CANVAS');
          var ctx = canvas.getContext('2d');
          var dataURL;
          canvas.height = this.naturalHeight;
          canvas.width = this.naturalWidth;
          ctx.drawImage(this, 0, 0);
          dataURL = canvas.toDataURL(outputFormat);
          callback(dataURL);
        };
        img.src = src;
        if (img.complete || img.complete === undefined) {
          img.src = "data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///ywAAAAAAQABAAACAUwAOw==";
          img.src = src;
        }
      }

      toDataURL(
        'testbild.jpg',
        function (dataUrl) {
          console.log('RESULT:', dataUrl)
          this.$router.push({
            name: "main",
            params: { uploaded_image: dataUrl, metaData: JSON.stringify(this.metaData) }
          });
        }.bind(this)
      )

      // toDataURL("testbild.jpg")
      //   .then(uploaded_image => {
